@import "./colors.scss";

.navigation {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: $navigationColor;

  &_logo {
    
    img {
        height: 70px;
    }
  }

  &_hamburger {
    display: none;
    position: relative;
    border: none;
    background: none;
    cursor: pointer;

    span {
      display: block;
      height: 2px;
      width: 40px;
      margin: 10px;
      background-color: #000;
    }

    &::after, &::before {
        content: '';
        position: absolute;
        display: block;
    height: 2px;
    width: 40px;top: -10px;
    margin: 10px;
    background-color: #000; 
    }

    &::after {
        top: 10px;
    }
  }

  &_list {
    display: flex;
    justify-content: flex-end;
    transition: transform .5s;

    &_item {
      margin: 0 15px;
      list-style: none;

      a {
        display: block;
        padding: 30px;
        color: #000;
        text-decoration: none;
        transition: color 0.3s;

        &:hover {
          color: red;
        }
      }
    }
  }
}


@media screen and (max-width: 900px) {

    .navigation {

        &_list {
            position: absolute;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            height: 100vh;
            width: 40vw;
            transform: translateX(100%);
            right: 0;
            top: 0;
            background-color: #fff;

            &_active {
                transform: translateX(0);
            }
        }

        &_hamburger {
            display: block;
        }
    }

}